import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Contact from './views/Contact.vue'
import Users from './views/Users.vue'
import Login from './views/Login.vue'
import Dashboard from './views/Dashboard.vue'
import AddUser from './views/AddUser' 
import axios from 'axios'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/users',
      name: 'users',
      component: Users,
      beforeEnter: (to, from, next) => {
        const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
        if (!token) {
          next('/login')
        } else {
          axios.post('/users/me', {}, {
            headers: {
              'Content-Type': 'application/json',
              'X-Auth': token.token
            }
          }).then(() => {
            next()
          }).catch(e => {
            console.log(e)
            next('login')
          });
        }
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
      beforeEnter: (to, from, next) => {
        const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
        if (!token) {
          next('/login')
        } else {
          axios.post('/users/me', {}, {
            headers: {
              'Content-Type': 'application/json',
              'X-Auth': token.token
            }
          }).then(() => {
            next()
          }).catch(e => {
            console.log(e)
            next('login')
          });
        }
      }
    },
    {
      path: '/new_user',
      name: 'new_user',
      component: AddUser,
      beforeEnter: (to, from, next) => {
        const token = JSON.parse(window.localStorage.getItem('binarySignalToken'))
        if (!token) {
          next('/login')
        } else {
          axios.post('/users/me', {}, {
            headers: {
              'Content-Type': 'application/json',
              'X-Auth': token.token
            }
          }).then(() => {
            next()
          }).catch(e => {
            console.log(e)
            next('login')
          });
        }
      }
    }
  ]
})
